@import 'colors';
@import 'globals';
@import 'helpers/mixins';
@import '../../components/Modals/modal';
@import '../../components/Header/Header';
@import '../../components/Footer/Footer';
@import '../../scenes/Main/HeroSearch';
@import '../../scenes/Forms/Forms';
@import '../../scenes/Forms/OrderForm';
@import '../../scenes/Item/Items';
@import '../../scenes/Forms/ReactTabs';
@import '../../scenes/Error404/Error';

body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  *, *::before,*::after { box-sizing: border-box; }
  *{ font-family: Roboto; }

  h1, h2, h3, h4, h5, h6, p {
    color: $grey-dark;
  }

  #root {
    min-width: 100%;
  }
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
